import { ChangeDetectionStrategy, ChangeDetectorRef, Component } from "@angular/core";


import { Platform } from "ionic-angular";
import { App, ModalController } from "ionic-angular";
import { NavController, } from "ionic-angular";

import { AppGlobals } from "@app/app.globals";
import { AppLocales } from "@app/app.locales";

import { MarsAuthService } from "@services/auth.service";
import { MarsInteractionService } from "@services/interaction.service";
import { MarsNavigationService } from "@services/navigation.service";

import { Backend } from "@backend/index";


@Component({
    selector: "page-promotion-history",
    templateUrl: "promotion-history.html"
})
export class PromotionHistoryPage {

    navigationService: MarsNavigationService;
    translations: AppTranslations;
    promotions: Array<any>;
    inventoryItemsCount: number;
    canCreate: boolean;
    allPromotions: any;
    currentStatus: string = "active";
    token: string;

    constructor(private platform: Platform,
        private app: App,
        private modalCtrl: ModalController,
        private navCtrl: NavController,
        private changeDetector: ChangeDetectorRef,
        private locales: AppLocales,
        private authService: MarsAuthService,
        private globals: AppGlobals,
        private interactionService: MarsInteractionService) {
        this.navigationService = new MarsNavigationService(this.app);
        this.navigationService.setNavCtrl(this.navCtrl);
        this.translations = this.locales.load();
    }

    ionViewCanEnter() {
        return this.navigationService.authCheck(PromotionHistoryPage.name, ['merchant']);
    }

    async ionViewWillEnter() {
        if (!MarsAuthService.hasRole('MERCHANT')) return;
        this.token = MarsAuthService.getMarsToken();
        await this.checkForInventoryItems();
        this.getPromotions();
    }

    async checkForInventoryItems() {
        try {
            this.inventoryItemsCount = (await Backend.getInventoryItemsCount({ xAccessToken: this.token })).data;
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    async getPromotions() {
        try {
            this.allPromotions = (await Backend.getPromotions({ xAccessToken: this.token })).data;
            this.promotions = this.allPromotions.active;
            this.canCreate = (this.inventoryItemsCount > 0) && !(this.allPromotions.active.length > 0);
        } catch (e) {
            this.interactionService.alert(this.translations.server_failure);
        } finally {
            this.changeDetector.detectChanges();
        }
    }

    filterPromotionsBy(status) {
        this.promotions = this.allPromotions[status];
    }

    goToPromotionPage(promotion: any = {}) {
        let id = promotion._id || "";
        this.navigationService.goTo("PromotionCategoriesPage", { promotionId: id });
    }

    async createPromotion() {
        let creationModal = this.modalCtrl.create("PromotionInformationPage");
        creationModal.present();
        creationModal.onWillDismiss(($event) => {
            if ($event.promotion) {
                this.interactionService.alert(this.translations.promotion_created_successfully);
                this.navigationService.goTo("PromotionCategoriesPage", { promotionId: $event.promotion._id });
            }
        });
    }
}



